@import 'https://fonts.googleapis.com/css?family=Montserrat:400,700,900&display=swap';

body {
  margin: 0;
  font-family: 'Montserrat';
  background-color: #fafafa;
  overflow-x: hidden;
}
.wgs-main {
  padding: 1rem;
}
.wgs-section-title {
  margin: 0.5rem 0;
}
a {
  text-decoration: none;
}
.link {
  font-weight: 600;
  color: #128ABA;
}
.link:hover {
  text-decoration: underline;
}
.link:active {
  color: #0D6182;
}